
import React, { useMemo, useState } from 'react';

import Box from '../../components/Box';
import Carousel from '../../components/Carousel';

import Board from './Board';
import { animals } from '../data';
import { responsive } from '../../contexts/responsive';

const GuidePage = ({ index }) => {
  const initialSlide = +index || 0
  const [species, setSpe] = useState()
  const filterAnimal = useMemo(() => animals.filter(d => {
    if(species) return d.species === species
    else return d
  }), [species])
  return (
    <Box
      pt={responsive('10%', '2%')}
      pb={responsive('2%', '2%')}
      px={responsive('5%', '8%')}
    >
      <Carousel nodot initialSlide={initialSlide}>
        {filterAnimal.map((d, i) => (
          <Board setSpe={setSpe} info={d} key={i} />
        ))}
      </Carousel>
    </Box>
  )
}

export default GuidePage;
