import React, { useState, useEffect, useRef } from 'react';
import styled from '@emotion/styled';
import range from 'lodash/range'

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import Box from './Box'
import Chevron from './Chevron'
import Flex from './Flex'
import Button from './Button'
import { responsive } from '../contexts/responsive';
import theme from '../@chakra-ui/gatsby-plugin/theme';

const StyleBox = styled(Box)`
  img,
  picture {
    display: block;
  }
  .slick-arrow {
    height: auto;
    font-size: 0.75em;
    z-index: 1;
    &::before {
      content: none;
    }
  }
  .slick-prev {
    width: 4rem;
    background: white;
    color: ${theme.colors.custom.blue};
    left: ${props => props.isMobile ? '0em' : '-8%'};
    :hover {
      background: white;
      color: ${theme.colors.custom.blue};
    }
  }
  .slick-next {
    width: 4rem;
    background: white;
    color: ${theme.colors.custom.blue};
    right: ${props => props.isMobile ? '0em' : '-8%'};
    :hover {
      background: white;
      color: ${theme.colors.custom.blue};
    }
  }
  @media screen and (max-width: 768px) {
    .slick-next, .slick-prev {
      width: 2.5em;
    }
  }
`

const Carousel = ({ children, nodot, width, autoplay, arrows, slidesToShow, sliderProps, initialSlide = 0, ...props }) => {
  const [activeItemIndex, setActiveIndex] = useState(initialSlide)
  const slickRef = useRef()
  useEffect(() => {
    if (slickRef.current) {
      slickRef.current.slickGoTo(activeItemIndex)
    }
  }, [slickRef, activeItemIndex])
  return (
    <StyleBox position="relative" width={width} {...props}>
      <Slider
        initialSlide={initialSlide}
        autoplay={autoplay}
        prevArrow={<Chevron.Left />}
        nextArrow={<Chevron.Right />}
        arrows={arrows}
        dots={false}
        ref={slickRef}
        autoplaySpeed={3500}
        speed={750}
        beforeChange={(from, to) => setActiveIndex(to)}
        {...sliderProps}
      >
        {children}
      </Slider>
      {!nodot && (
        <Flex
          position="absolute"
          bottom="1em"
          left="50%"
          transform="translateX(-50%)"
        >
          {range(children.length).map((i) => (
            <Box mx="0.25em" key={i}>
              <Button
                border="1px solid"
                borderColor="lightGray"
                bg="lightGray"
                hoverBorder="white"
                hoverBg="white"
                py={responsive('0.375rem', '0.5rem')}
                px={responsive('0.375rem', '0.5rem')}
                active={i === activeItemIndex}
                onClick={() => setActiveIndex(i)}
              />
            </Box>
          ))}
        </Flex>
      )}
    </StyleBox>
  );
}

Carousel.defaultProps = {
  arrows: true,
}

export default Carousel;
