import React from 'react'
import { isArray } from 'lodash';
import { StaticImage } from 'gatsby-plugin-image';

import Box from '../../components/Box'
import Text from '../../components/Text'
import Flex from '../../components/Flex'
import SwingAnime from '../../components/SwingAnime';
import { responsive } from '../../contexts/responsive';

import Species from './Species';

export const size = (d) => `${d / 1920}px`

const Board = ({ info, setSpe }) => {
  return (
    <Flex flexDirection={responsive('column', 'row')} alignItems="center" mx="0.5em">
      <Box flex={1} pr={responsive(0, '3.125em')}>
        <SwingAnime>
          {info.guide}
        </SwingAnime>
        <StaticImage layout="constrained" src="shadow.png" />
      </Box>
      <Box width={responsive('100%', '50%')} color="white" pl={responsive(0, '3.125em')}>
        <Species setSpe={setSpe} species={info.species} />
        <Text letterSpacing="0.25rem" mt={responsive('1.25rem', '0.62em')} lineHeight="1.2" whiteSpace="pre-wrap" fontWeight="bold" fontSize={responsive('1.5em', '4em')}>{info.name}</Text>
        <Text mt={responsive('0', '-0.5rem')} fontStyle="italic" lineHeight="1" fontSize={responsive('1em', '2.5em')}>{info.eng}</Text>
        <Text textAlign="justify" mt={responsive('0.75rem', '1.133em')} lineHeight="1.8" letterSpacing="0.125rem" fontSize={responsive('0.75em', '1.875em')}>{info.desc}</Text>
        <Flex>
          {info.data && info.data.map((d, k) => (
            <Flex mt={'1.75em'} key={k}>
              {isArray(d.icon) ? d.icon.map((icon, key) => (
                <Flex alignItems="center" ml={key && '1em'} key={key}>
                  <Box color="white" width={responsive("1.875em", '5em')} mr={'1em'}>
                    {icon}
                  </Box>
                  <Text.Bold fontSize={responsive('1em', '1.875em')}>{d.text[key]}</Text.Bold>
                </Flex>
              )) : d.icon && (
                <Flex alignItems="center" color="white" mr={'1em'}>
                  <Box width={responsive("1.875em", '5em')} mr={'1em'}>{d.icon}</Box>
                  <Text.Bold fontSize={responsive('1em', '1.875em')}>{d.text}</Text.Bold>
                </Flex>
              )}
            </Flex>
          ))}
        </Flex>
      </Box>
    </Flex>
  )
}

export default Board
