import React from 'react'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import { responsive } from '../../contexts/responsive';
import { size } from './Board';

const labels = ['魚類', '無脊椎動物', '底質']

const Species = ({ species, setSpe, ...props }) => {
  return (
    <Flex {...props}>
      {labels.map((spe, i) => (
        <Box
          width={spe.length / 9}
          pt={responsive('0.5rem', '0.75rem')}
          pb={responsive('0.25rem', '0.375rem')}
          textAlign="center"
          bg={species === spe && 'white'}
          color={species === spe && 'custom.blue'}
          borderLeft={!i && '2px solid white'}
          borderRight="2px solid white"
          borderTop="2px solid white"
          borderBottom="2px solid white"
          borderRadius={!i ? '1rem 0 0 1rem' : i % 2 ? 'none' : '0 1rem 1rem 0'}
          fontSize={responsive('1em', '1.875em')}
          fontWeight="bold"
          letterSpacing="0.25rem"
          onClick={() => setSpe(spe)}
          cursor={'pointer'}
          key={i}
        >
          {spe}
        </Box>
      ))}
    </Flex>
  )
}

export default Species
